<script>
export default {
  props: {
    youtubeUrl: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      activeTab: 'info',
      tabs: [
        { id: 'info', name: 'ÂïÜÂìÅÂΩ±Áâá' },
        { id: 'spec', name: 'Ë¶èÊ†ºË™™Êòé' },
        { id: 'notice', name: 'Ë≥ºË≤∑È†àÁü•' },
        { id: 'qa', name: 'ÂïèËàáÁ≠î' }
      ]
    }
  },
  methods: {
    // Âà§Êñ∑ÊòØÂê¶ÁÇ∫ YouTube Á∂≤ÂùÄ
    isYouTubeUrl (url) {
      if (typeof url !== 'string') return false
      return url.includes('youtube.com/watch') || url.includes('youtu.be/')
    },
    // Â∞áÁ∂≤ÂùÄËΩâÊèõÁÇ∫ YouTube ÂµåÂÖ•Á∂≤ÂùÄ
    embedYouTubeUrl (url) {
      let videoId = ''
      if (url.includes('youtube.com/watch')) {
        const urlParams = new URLSearchParams(url.split('?')[1])
        videoId = urlParams.get('v')
      } else if (url.includes('youtu.be/')) {
        videoId = url.split('youtu.be/')[1]
      }
      return `https://www.youtube.com/embed/${videoId}`
    }
  }
}
</script>
