  },
  methods: {
    getProduct () {
      this.isLoading = true
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/product/${this.id}`
      this.$http.get(api)
        .then(res => {
          this.isLoading = false
          if (res.data.success) {
            this.product = res.data.product
          }
        })
        .catch((err) => {
          console.error('API ÈåØË™§Ôºö', err)
          this.showToast('error', 'Ë≥áÊñôËºâÂÖ•Â§±Êïó')
        })
    },
    addToCart (id, qty = 1) {
      this.isLoading = true
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`
      const cart = {
        product_id: id,
        qty
      }
      this.$http.post(api, { data: cart })
        .then(res => {
          this.isLoading = false
          this.showToast('success', 'Â∑≤Âä†ÂÖ•Ë≥ºÁâ©Ëªä')
          this.$router.push('/cart')
        })
        .catch((err) => {
